}

a {
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

