.page-title {
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  color: #3CA3CE;
  text-align: center;
  font-family: "Philosopher";
  font-size: 64px;
  font-size: 4rem;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  padding-left: 36px;
  padding-right: 36px;
  margin-top: 40px;
  margin-bottom: 106px;
}
@media (max-width: 1280px) {
  .page-title {
    margin-bottom: 80px;
  }
}
@media (max-width: 1024px) {
  .page-title {
    font-size: 52px;
    font-size: 3.25rem;
    margin-top: 36px;
    margin-bottom: 60px;
  }
}
@media (max-width: 960px) {
  .page-title {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 768px) {
  .page-title {
    margin-top: 30px;
    margin-bottom: 42px;
  }
}
@media (max-width: 599px) {
  .page-title {
    font-size: 46px;
    font-size: 2.875rem;
    margin-top: 26px;
    margin-bottom: 36px;
  }
}
@media (max-width: 420px) {
  .page-title {
    font-size: 42px;
    font-size: 2.625rem;
    margin-top: 20px;
    margin-bottom: 32px;
  }
}
.page-title-back {
  position: absolute;
  color: #C73752;
  font-family: "Inter";
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 150%;
  text-decoration-line: underline;
  display: inline-block;
  text-transform: none;
  left: 50%;
  transform: translateX(-50%);
  bottom: -31px;
  transition: color 0.3s;
  white-space: nowrap;
}
@media (max-width: 768px) {
  .page-title-back {
    bottom: -26px;
  }
}
@media (max-width: 420px) {
  .page-title-back {
    font-size: 14px;
    font-size: 0.875rem;
    bottom: -22px;
  }
}
.page-title-back:hover {
  color: #DA3C5A;
}
.page-title::before {
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
  content: "";
  background-image: url(../images/title-img.svg);
  width: 190px;
  height: 6px;
  display: block;
}
@media (max-width: 960px) {
  .page-title::before {
    display: none;
  }
}
.page-title::after {
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  content: "";
  background-image: url(../images/title-img.svg);
  width: 190px;
  height: 6px;
  display: block;
}
@media (max-width: 960px) {
  .page-title::after {
    display: none;
  }
}

