.ui-field {
  position: relative;
}
.ui-field label.error {
  color: #CB0000;
  font-size: 12px;
  font-size: 0.75rem;
  width: max-content;
}
.ui-field span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
  cursor: text;
  font-size: 14px;
  font-size: 0.875rem;
}
.ui-field input:invalid + span::before {
  content: attr(placeholder);
  position: relative;
  color: #EAF5FF;
}
.ui-field input:invalid + span::after {
  content: "*";
  color: #C73752;
}

.ui-input {
  border: 1px solid #fff;
  color: #fff;
  background: transparent;
  padding: 18px;
  outline: none;
  border-radius: 6px;
  width: 100%;
}
@media (max-width: 960px) {
  .ui-input {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media (max-width: 768px) {
  .ui-input {
    font-size: 16px;
    font-size: 1rem;
  }
}
.ui-input-wrapper {
  position: relative;
}
.ui-input::placeholder {
  color: #EAF5FF;
}

.ui-textarea {
  border: 1px solid #fff;
  color: #fff;
  background: transparent;
  padding: 1.125rem;
  outline: none;
  border-radius: 0.375rem;
  width: 100%;
  height: 104px;
  resize: none;
}
.ui-textarea::placeholder {
  color: #EAF5FF;
}

.ui-btn {
  border: none;
  color: #fff;
  outline: none;
  background-color: #C73752;
  padding: 1.125rem 2.3125rem;
  border-radius: 2.25rem;
  cursor: pointer;
  transition: background-color 0.3s;
}
@media (max-width: 599px) {
  .ui-btn {
    border-radius: 6px;
  }
}
.ui-btn:hover {
  background-color: #DA3C5A;
}

