.wrapper {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -45%);
  width: 101vw;
  height: 100vw;
  max-width: 1930px;
  max-height: 1920px;
  border-radius: 50%;
  background: linear-gradient(90deg, rgba(29, 114, 193, 0.0760679272) 0%, rgba(246, 251, 255, 0.7903536415) 53%, rgba(29, 114, 193, 0.1264880952) 90%);
  z-index: -1;
}
@media (max-width: 768px) {
  .wrapper::before {
    display: none;
  }
}

.header {
  margin-bottom: 10px;
  position: relative;
  z-index: 12;
}
.header-menu {
  background-color: #fff;
}
.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-contacts {
  display: flex;
  align-items: flex-start;
  width: calc(50% - 195px / 2);
  padding-left: 54px;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding-top: 12px;
  gap: 12px;
}
@media (max-width: 1500px) {
  .header-contacts {
    width: calc(50% - 180px / 2);
    padding-left: 40px;
  }
}
@media (max-width: 1366px) {
  .header-contacts {
    width: calc(50% - 165px / 2);
    padding-left: 34px;
  }
}
@media (max-width: 960px) {
  .header-contacts {
    padding-top: 0;
    width: calc(50% - 120px / 2);
    flex-direction: row;
    row-gap: unset;
  }
}
.header-contacts-item {
  display: flex;
  gap: 6px;
}
@media (max-width: 1024px) {
  .header-contacts-item__desktop {
    display: none;
  }
}
.header-contacts-item__mobile {
  display: none;
